@import "../../inc/vars";

.acrobits_softphone-forms {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
  }
  @media (min-width: $screen-sm){
    flex-direction: row;
  }
  &__form {
    width: 100%;
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 32px 16px;
    justify-content: center;
    position: relative;

    .acrobits_softphone-forms__bottom-image {
      max-width: 100%;
      height: auto;
      align-self: center;
      display: none;
      margin-top: 20px;
      @media (min-width: $screen-sm){
        display: inline-block;
      }
      @media (min-width: $screen-md){
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .purple-link {
      color: #8D6DD2;
      text-decoration: underline;
      @include fig(400, 20, 1.56);
      &:hover {
        color: $purple;
      }
    }
    @media (min-width: $screen-sm){
      width: 42%;
      padding: 64px 72px 0;
    }
    @media (min-width: 1024px) and (max-height: 900px){
      padding: 44px 72px 0;
    }
    @media (min-width: $screen-md){
      padding-bottom: 180px;
      justify-content: flex-start;
    }
    &__subtitle {
      display: flex;
      flex-wrap: wrap;
      @include titleGrad();
      @include fig(500, 20, 1.24);
      text-align: center;
      justify-content: center;
      align-items: center;
      padding-bottom: 8px;
      &.step-2 {
        font-family: 'Poppins', sans-serif;
        font-weight: 400;
      }
      @media (min-width: $screen-sm){
        text-align: left;
        justify-content: flex-start;
        padding-bottom: 14px;
      }
      img {
        margin-left: 8px;
      }
    }
    .gform_wrapper {
      .gform_heading {
        display: none;
      }
      .gform_body {
        .gform_fields {
          grid-row-gap: 24px;
          @media (min-width: $screen-sm){
            grid-row-gap: 29px;
          }
          @media (min-width: 1024px) and (max-height: 900px){
            grid-row-gap: 20px;
          }
          .gfield {
            &_label {
              @include fig(400, 20, 1);
              color: #000;
              margin-bottom: 8px;
              @media (min-width: 1024px) and (max-height: 900px){
                margin-bottom: 6px;
              }
            }
            .gfield_required {
              display: none;
            }
            input[type=text],
            input[type=email],
            input[type=password],
            input[type=number] {
              width: 100%;
              padding: 16px 20px;
              border-radius: 4px;
              border: 2px solid var(--Background-300, #BABFC9);
              font-size: 23px;
              line-height: 156%;
              @include fig(400, 23, 1.56);
              @media (min-width: 1024px) and (max-height: 900px){
                padding: 12px 20px;
              }
              &::placeholder {
                color: #8C8698;
              }
              &:active,
              &:focus {
                border-color: #000;
              }
            }
          }
        }
        .gform_page {
          .acrobits_softphone-forms__form-title {
            @include pop(400, 34, 1.24);
            @include titleGrad();
            text-align: center;
            margin: 0;
            letter-spacing: -0.02em;
            @media (min-width: $screen-sm){
              font-size: 36px;
              text-align: left;
            }
            @media (min-width: 1024px) and (max-height: 900px){
              font-size: 30px;
            }
          }
        }
        .gform_page_footer {
          display: flex;
          flex-direction: column;
          align-items: center;
          @media (min-width: $screen-sm){
            justify-content: center;
          }
          input[type=button], input[type=submit] {
            @include greenButton();
            background: $purple;
            border: none;
            cursor: pointer;
            width: 450px !important;
            max-width: 100%;
            margin: 0;
            box-shadow: none;
            &:hover {
              background: #532D9F;
            }
          }
          .gform_previous_button {
            display: none !important;
            height: auto !important;
            background: none !important;
            color: #8D6DD2 !important;
            padding: 0;
            box-shadow: none;
            @include fig(500, 17, 1.17);
            position: relative;
            margin-bottom: 10px !important;
            &:hover {
              color: $purple !important;
            }
          }
        }
      }
      .booking-form__cal-head {
        text-align: center;
        @include pop(400, 24, 1.24);
        @include titleGrad();
        letter-spacing: -0.02em;
        padding-top: 28px;
        @media (min-width: $screen-sm){
          font-size: 28px;
        }
      }
    }
  }
  &__logo {
    margin-bottom: 16px;
    text-align: center;
    @media (min-width: $screen-sm){
      margin-bottom: 24px;
      text-align: left;
    }
    @media (min-width: 1024px) and (max-height: 900px){
      margin-bottom: 16px;
    }
  }
  &__title-box {
    margin-bottom: 21px;
    @media (min-width: $screen-sm){
      margin-bottom: 121px;
    }
    @media (min-width: 1024px) and (max-height: 900px){
      margin-bottom: 80px;
    }
  }
  &__title {
    @include pop(500, 29, 1.11);
    letter-spacing: -0.01em;
    margin-bottom: 10px;
    @media (min-width: $screen-sm){
      font-size: 35px;
      margin-bottom: 39px;
    }
    @media (min-width: 1024px) and (max-height: 900px){
      font-size: 32px;
    }
  }
  &__subtitle {
    @include pop(500, 24, 1.56);
    letter-spacing: -0.01em;
  }
  &__image-wrapper{
    position: relative;
    display: flex;
    justify-content: center;
    width: auto;
    max-width: 100%;
    margin: 0 auto;
    flex-wrap: wrap;
    .features {
      .feature {
        border-radius: 12px;
        background: var(--grad-linkup, linear-gradient(180deg, #DEF8EF 0%, #FFF 100%));
        box-shadow: 30.95px 35.371px 38px 0px rgba(0, 0, 0, 0.08), 11.921px 13.624px 12.104px 0px rgba(0, 0, 0, 0.04), 2.522px 2.882px 3.096px 0px rgba(0, 0, 0, 0.01);
        padding: 20px 20px 20px 52px;
        position: relative;
        color: #000;
        margin-bottom: 10px;
        @include pop(400, 18, 1.24);
        &:before {
          content: '';
          display: block;
          width: 24px;
          height: 24px;
          position: absolute;
          left: 20px;
          top: 20px;
          background: url('/wp-content/themes/acro/images/svg/checked-dark.svg') no-repeat center center;
        }
        @media (min-width: $screen-sm) {
          display: inline-block;
          position: absolute;
          z-index: 2;
          opacity: 0;
          &.feature1 {
            left: 25%;
            top: -12%;
            animation: acrobitsSlideInUp 1s ease-in-out 0s forwards;
          }
          &.feature2 {
            right: -1%;
            top: 16%;
            animation: acrobitsSlideInUp 1s ease-in-out 0.5s forwards;
          }
          &.feature3 {
            left: -2%;
            top: 41%;
            animation: acrobitsSlideInUp 1s ease-in-out 1s forwards;
          }
          &.feature4 {
            left: 18%;
            bottom: 0;
            animation: acrobitsSlideInUp 1s ease-in-out 1.5s forwards;
          }
        }
      }
    }
  }
  &__images {
    position: relative;
    display: inline-block;
    padding-top: 40px;
    @media (min-width: $screen-sm){
      padding-top: 0;
    }
    img {
      max-width: 100%;
    }
    .acrobits_softphone-forms__image2 {
      position: absolute;
      right: 5%;
      bottom: -3%;
      max-width: 28%;
    }
  }
  &__content {
    width: 100%;
    background-size: cover;
    background-color: #3A1F6E;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('/wp-content/themes/acro/images/svg/bg-lines.svg'), linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%);
    padding: 32px 16px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media (min-width: $screen-sm){
      width: 58%;
      padding: 64px 72px;
    }
    @media (min-width: 1024px) and (max-height: 900px){
      padding: 44px 72px;
    }
    &-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  &_style-1 {
    @media (min-width: $screen-mlg){
      max-height: 100vh;
    }
    .acrobits_softphone-forms__content-wrapper {
      display: flex !important;
    }
    &[data-current_page="1"]{
      .gform_page_footer {
        input[type=button], input[type=submit] {
          background: $green !important;
          &:hover {
            background: #24a87d !important;
          }
        }
      }
    }
    .gform_wrapper {
      .gform_fields {
        .gfield {
          &--type-radio {
            padding: 22px 50px 22px 20px;
            border-radius: 4px;
            border: 2px solid var(--Background-300, #BABFC9);
            font-size: 23px;
            line-height: 156%;
            position: relative;
            @media (min-width: 1024px) and (max-height: 900px){
              padding: 18px 50px 18px 20px;
            }
            &.active {
              border-color: #000;
              .ginput_container_radio {
                border-color: #000 !important;
              }
              .gfield_label {
                color: #000 !important;
                &:after {
                  filter: brightness(0.1);
                }
              }
            }
            .gfield_label {
              font-size: 23px;
              color: #8C8698;
              position: relative;
              top: 0;
              width: 100%;
              cursor: pointer;
              margin-bottom: 0;
              //transform: translateY(calc(50% + 20px));
              //&:before {
              //  content: '';
              //  display: block;
              //  width: calc(100%);
              //  height: 2px;
              //  background: #BABFC9;
              //  top: -21px;
              //  left: 0;
              //  position: absolute;
              //}
              &:after {
                content: '';
                display: block;
                width: 20px;
                height: 20px;
                background: #BABFC9;
                top: 50%;
                transform: translateY(-50%) rotate(90deg);
                right: -30px;
                position: absolute;
                background: url('/wp-content/themes/acro/images/svg/arrow-right.svg') no-repeat center;
              }
            }
            .ginput_container_radio {
              position: absolute;
              display: none;
              width: calc(100% + 4px);
              left: -2px;
              top: calc(100% - 2px);
              border: 2px solid var(--Background-300, #BABFC9);
              border-top: none;
              background: #fff;
              padding: 0 20px 20px;
              border-radius: 0 0 4px 4px;
              .gfield_radio {
                position: relative;
                z-index: 2;
                label {
                  font-size: 15px;
                  color: #000;
                  position: relative;
                  padding-left: 28px;
                  cursor: pointer;
                  &:before {
                    content: '';
                    display: block;
                    width: 16px;
                    height: 16px;
                    border: 4px solid #B5B7B9;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: calc(50% - 8px);
                  }
                }
                input:checked + label {
                  &:before {
                    border-color: #1E8F69;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &_style-2 {
    @media (min-width: $screen-mlg){
      //max-height: 100vh;
      & .step-2-wrapper .step-2 {
        height: 100vh;
        margin-top: 155px;
        justify-content: unset;
      }
    }
    .acrobits_softphone-forms {
      &__form {
        .gform_wrapper {
          .gform_body {
            .gform_page_footer {
              flex-direction: column;
              align-items: center;
            }
          }
        }
      }
      

      &__content {
        background-color: #EEEFF2;
        background-image: url('/wp-content/themes/acro/images/svg/bg-lines.svg'), linear-gradient(180deg, #EEEFF2 0%, #EEEFF2 33.29%, #EEEFF2 96.88%);
        color: #515354;
        text-align: center;
        padding: 53px 16px;
      }
      &__title-box {
        @media (min-width: $screen-sm){
          padding: 0 20%;
        }
      }
      &__title {
        margin-bottom: 14px;
      }
      &__image-wrapper {
        text-align: left;
      }

      &__content-wrapper {
        &.step-3 {
          .acrobits_softphone-forms__title-box {
            margin-bottom: 60px;
            @media (min-width: $screen-sm){
              margin-bottom: 64px;
            }
          }
          .acrobits_softphone-forms__image-wrapper {
            &__image {
              opacity: 0;
              img {
                border-radius: 12px;
                box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
              }
            }
            flex-direction: column;
            .top-row {
              display: flex;
              justify-content: center;
              width: 100%;
              margin-bottom: 10px;
              @media (min-width: $screen-sm){
                margin-bottom: 24px;
              }
            }
            .bottom-row {
              display: flex;
              gap: 10px;
              @media (min-width: $screen-sm){
                gap: 20px;
              }
              img {
                max-width: 40vw;
              }
            }
          }
        }
      }
    }

    .gform_wrapper {
      .gform_fields {
        .gfield {
          &--type-radio {
            .gfield_label {
              margin-bottom: 20px;
            }
            .ginput_container_radio {
              position: relative;
              .gfield_radio {
                display: flex;
                gap: 60px;
                label {
                  font-size: 18px;
                  color: #4F4B58;
                  position: relative;
                  padding-left: 28px;
                  cursor: pointer;
                  font-weight: 400;
                  &:before {
                    content: '';
                    display: block;
                    width: 16px;
                    height: 16px;
                    border: 4px solid #B5B7B9;
                    border-radius: 50%;
                    position: absolute;
                    left: 0;
                    top: calc(50% - 8px);
                  }
                }
                input:checked + label {
                  &:before {
                    border-color: #1E8F69;
                  }
                }
              }
            }
          }
        }
      }
    }
    max-height: min-content;

    @media (min-width: 766px) and (max-height: 750px) {
      max-height: none; /* Remove max-height constraint for this range */
    }

    @media (max-height: 750px) {

      max-height: none; /* Unset max-height for any screen smaller than 750px in height */

    }
    &[data-current_page="1"]{
      .step-2, .step-3, .step-4 {
        display: none;
      }
    }
    &[data-current_page="2"]{
      max-height: 100vh;

      @media (min-width: 766px) and (max-height: 750px) {
        max-height: none; /* Remove max-height constraint for this range */
      }

      @media (max-height: 750px) {

        max-height: none; /* Unset max-height for any screen smaller than 750px in height */

      }

      .step-1, .step-3, .step-4 {
        display: none;
      }
      .acrobits_softphone-forms__content-wrapper {
        &.step-2 {
          .acrobits_softphone-forms__title-box {
            margin-bottom: 60px;
          }
          .acrobits_softphone-forms__image-wrapper {
            .acrobits_softphone-forms__slides__slides {
              display: flex;
              justify-content: center;
              flex-wrap: wrap;
              position: relative;
              &:before {
                content: '';
                position: absolute;
                display: block;
                width: 400px;
                height: 350px;
                border-radius: 14.964px;
                opacity: 0.6;
                background: var(--purple-purple-600, #543695);
                filter: blur(93.52574157714844px);
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
              }
              .slide {
                position: relative;
                @media (min-width: $screen-sm){
                  max-width: 50%;
                }
              }
              .slide-image {
                img {
                  width: 694px;
                  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
                  border-radius: 15px;
                  max-width: 100%;
                  height: auto;
                }
              }
              .slide-logo {
                margin: 20px 0;
                display: flex;
                align-items: center;
                @include pop(400, 8, 1.24);
                color: #515354;
                img {
                  margin: 0 15px 0 0;
                  @media (min-width: $screen-sm){
                    margin: 0 20px 0 0;
                  }
                }
                @media (min-width: $screen-sm){
                  @include pop(400, 13, 1.24);
                }
              }
              .slide1 {
                top: 50px;
                animation: acroPulse 8s infinite;
                display: flex;
                flex-direction: column;
                @media (min-width: $screen-sm){
                  right: -150px;
                  top: auto;
                }
                img {
                  animation: acroGrayscale 8s infinite;
                }
                .slide-logo {
                  order: -1;
                  img {
                    width: 50px;
                    @media (min-width: $screen-sm){
                      width: 100px;
                    }
                  }
                }
              }
              .slide2 {
                top: -120px;
                animation: acroPulse 8s infinite;
                animation-delay: 4s;
                @media (min-width: $screen-sm){
                  left: -150px;
                  top: 120px;
                }
                img {
                  animation: acroGrayscale 8s infinite;
                  animation-delay: 4s;
                }
                .slide-logo {
                  justify-content: flex-end;
                  img {
                    margin: 0 0 0 10px;
                    width: 50px;
                    @media (min-width: $screen-sm){
                      margin: 0 0 0 20px;
                      width: 60px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    &[data-current_page="3"]{
      .step-1, .step-2:not(.acrobits_softphone-forms__form__subtitle) {
        display: none;
      }
      .acrobits_softphone-forms__title-box {
        padding: 0;
        margin-bottom: 60px;
        @media (min-width: $screen-sm) {
          margin-bottom: 30px;
        }
      }
      .step-3 {
        .acrobits_softphone-forms__image-wrapper {
          &__image {

            &.image-1 {
              animation: acrobitsFadeInUpLeft 1s ease-in-out 0s forwards;
            }
            &.image-2 {
              animation: acrobitsFadeInUpLeft 1s ease-in-out 0.3s forwards;
            }
            &.image-3 {
              animation: acrobitsFadeInUpLeft 1s ease-in-out 0.6s forwards;
            }
            &.image-4 {
              animation: acrobitsFadeInUpLeft 1s ease-in-out 0.9s forwards;
            }

          }
        }
      }
    }
    &[data-current_page="4"]{
      .acrobits_softphone-forms__image-wrapper {
        display: block;
        margin: 0;
        max-width: 100%;
      }
    }
  }
  &[data-current_page="1"]{
    .acrobits_softphone-forms__form {
      @media (min-width: $screen-md){
        padding-bottom: 64px;
      }
      @media (min-width: 1024px) and (max-height: 900px){
        padding-bottom: 44px;
      }
      .acrobits_softphone-forms__bottom-image {
        display: none;
      }
      .acrobits_softphone-forms__form__subtitle {
        &.step-2 {
          display: none;
        }
      }
    }
  }
  &[data-current_page="2"]{
    @media (min-width: $screen-sm){
      .gfield--type-ssa_appointment {
        max-height: 491px;
        overflow: hidden;
      }
    }
    .acrobits_softphone-forms__form .gform_wrapper .gform_body .gform_page_footer .gform_previous_button {
      display: inline-block !important;
    }
    .acrobits_softphone-forms__form {
      .gform_button[type="submit"] {
        display: none !important;
      }
      .acrobits_softphone-forms__form__subtitle {
        &.step-2 {
          display: flex;
        }
        &.step-1 {
          display: none;
        }
        &.step-3 {
          display: none;
        }
        &.step-4 {
          display: none;
        }
      }
    }
  }
  &[data-current_page="3"]{
    .step-2:not(.acrobits_softphone-forms__form__subtitle), .step-1, .step-4 {
      display: none;
    }
    .gform_button[type="submit"] {
      display: none !important;
    }
  }
  &[data-current_page="4"]{
    .acrobits_softphone-forms__title-box {
      margin-bottom: 10px;
    }
    //.acrobits_softphone-forms__form .gform_wrapper .gform_body .gform_page_footer .gform_previous_button {
    //  display: inline-block !important;
    //}
  }
  &[data-current_page="1"]{
    .step-3:not(.acrobits_softphone-forms__form__subtitle), .step-3, .step-4 {
      display: none;
    }
  }
  &[data-current_page="2"]{
    .step-1, .step-3, .step-4 {
      display: none;
    }
  }
  &[data-current_page="4"]{
    .step-1, .step-3, .step-2 {
      display: none;
    }
  }
  .acrobits_softphone-forms__form {
    .gform_page {
      &:first-child {
        .gform_page_footer {
          justify-content: flex-start !important;
          align-items: flex-start !important;
          input[type=button], input[type=submit] {
            width: 240px !important;
          }
        }
      }
    }
  }
}

@keyframes acrobitsSlideInUp {
  from {
    opacity: 0;
    transform: translateY(200%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes acrobitsFadeInUpLeft {
  from {
    opacity: 0;
    transform: translate(20%, 20%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes acroPulse {
  0% {
    transform: scale(0.95) translate(-10px, 10px);
    opacity: 0.8;
    z-index: 1;
  }
  20% {
    transform: scale(1);
    z-index: 2;
    opacity: 1;
  }
  50% {
    transform: scale(1);
    z-index: 2;
    opacity: 1;
  }
  70% {
    transform: scale(0.95) translate(-10px, 10px);
    opacity: 0.8;
    z-index: 1;
  }
  100% {
    transform: scale(0.95) translate(-10px, 10px);
    opacity: 0.8;
    z-index: 1;
  }
}

@keyframes acroGrayscale {
  0% {
    filter: grayscale(1);
  }
  20% {
    filter: grayscale(0);
  }
  50% {
    filter: grayscale(0);
  }
  70% {
    filter: grayscale(1);
  }
  100% {
    filter: grayscale(1);
  }
}